
.Navbar-wrapper {
    position: absolute;
    display: block;
    top: 0;
    margin: auto;
    color: white;
    width: 100%;
    text-align: center;
}

.Navbar {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	margin: auto;
}


.Navbar-link {
	display: inline-block;
	margin: 1em;
	-ms-flex: 1 1;
	    flex: 1 1;
	text-transform: uppercase;
}



