.Fixed {
	position: absolute;
	top: 0;
	height: 100vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}

.PrevButton {
	left: 1em;
}

.NextButton {
	right: 1em;
}

button.FixedButton {
	background: none;
	border: none;
	text-decoration: none;
	cursor: pointer;
	margin: 1em;
	font-size: 3em;
	-webkit-transition: -webkit-transform 300s;
	transition: -webkit-transform 300s;
	-o-transition: transform 300s;
	transition: transform 300s;
	transition: transform 300s, -webkit-transform 300s;
}

button.FixedButton:hover {
	-webkit-transform: scale(1.3);
	    -ms-transform: scale(1.3);
	        transform: scale(1.3);
}

button.FixedButton:active {
	-webkit-transform: scale(0.7);
	    -ms-transform: scale(0.7);
	        transform: scale(0.7);
}



