.Slide {
	position: absolute;
	top: 0;
	left: 0;
	background: transparent;
	-webkit-transform-origin: center;
	    -ms-transform-origin: center;
	        transform-origin: center;
	width: 100%;
	height: 100%;
	color: lightgray;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}

.SlideShow-wrapper {
	width: 100vw;
	height: 100vh;
}

.Controls {
	position: absolute;
	bottom: 0;
	width: 100%;
	z-index: 101;
	text-align: center;
	margin-bottom: 1.4em;
}

.Controls > button {
	cursor: pointer;
	font-size: 1em;
}
.Controls > button {
	width: 2em;
	height: 2em;
	border-left: 1px solid white;
	border-top: 1px solid white;
	-webkit-transform: rotateZ(-135deg);
	    -ms-transform: rotate(-135deg);
	        transform: rotateZ(-135deg);
}



