.White-background-wrapper {
	background-color: white;
	width: 100%;
}

.Events-wrapper {
	display: block;
	max-width: 768px;
	margin: 0 auto 1em auto;
	padding-top: 5em;
	background-color: white;
	color: #222122;
}

.Events-heading {
	margin-bottom: 12px;
}

.Events {
	display:  -ms-flexbox;
	display:  flex;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;

	-ms-flex: 1 1 360px;

	    flex: 1 1 360px;

	-ms-flex-pack: start;

	    justify-content: flex-start;
	-ms-flex-align: stretch;
	    align-items: stretch;

}

@media only screen and (max-width: 768px) {
	.Events {
		width: 100%;
		-ms-flex-flow: column;
		    flex-flow: column;
		white-space: normal;
	}

	.Events-wrapper {
		margin-left: 1em;
		margin-right: 1em;
	}
}

small {
	text-transform: uppercase;
	color: #777;
	font-size: 0.7em;
}

.Events-selected {
	position: absolute;
	margin: 0;
	border: none;
	-webkit-box-shadow: none;
	        box-shadow: none;
	z-index: 10000;
}


