input.TextInput-input {
	width: 100%;
	background: none;
	border: none !important;
	margin: 0;
	font-family: inherit;
	font-weight: 700;
	-webkit-transition: color 1s;
	-o-transition: color 1s;
	transition: color 1s;
}

input.TextInput-input:focus {
	border: none !important;
}

input[type="text"].TextInput-input:focus {
	border: none !important;
}


input:focus {
	outline: none;
}

input.TextInput-input::-webkit-input-placeholder {
	color: lightgray;
}

input.TextInput-input:-ms-input-placeholder {
	color: lightgray;
}

input.TextInput-input::placeholder {
	color: lightgray;
}



