.ShortEvent-wrapper {
	width: 100%;
	text-align: center;
	z-index: 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	-webkit-transition: all 500ms;
	-o-transition: all 500ms;
	transition: all 500ms;
}

.ShortEvent-selected {
	margin-bottom: 1em;
	margin-top: 1em;
	-webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
	        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.ShortEvent {
	padding: 1em 0;
	cursor: pointer;
	display: inline;
	width: 100%;
	font-size: 2em;
	-webkit-transition: font-size 500ms;
	-o-transition: font-size 500ms;
	transition: font-size 500ms;
}

.ShortEvent:hover {
	font-size: 2.1em;
}

.centeredDiv {
	vertical-align: middle;
	width: 100%;
}

.ShortEvent-title {
	font-weight: 900;
	width: 100%;
}

.ShortEvent-description {
	text-align: center;
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	color: #e6ffe0;
	-ms-flex-align: center;
	    align-items: center;
}

.ShortEvent-background {
	content: '';
	position: absolute;
	z-index: -1;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	-webkit-filter: grayscale(1) brightness(0.4) saturate(4);
	        filter: grayscale(1) brightness(0.4) saturate(4);
	-webkit-transition: -webkit-filter 500ms;
	transition: -webkit-filter 500ms;
	-o-transition: filter 500ms;
	transition: filter 500ms;
	transition: filter 500ms, -webkit-filter 500ms;
	background-repeat: none;
}

@media only screen and (max-width: 768px) {
	.ShortEvent {
		display: inline;
		border: none;
		text-align: left;
		-webkit-box-shadow: none;
		        box-shadow: none;
		font-size: 1.5em;
	}


	.ShortEvent:after {
		border-radius: none;
		display: none;
	}


	.ShortEvent:hover {
		font-size: 1.5em;
	}

}

.ShortEvent-wrapper {
	background-color: #196070;
}

.ShortEvent-background {
	display: none;
}


.ShortEvent-coverphoto {
}

.EventLinks {
	display: block;
	width: 100%;
	background: #E6FFE0;
	text-align: left;
}
.EventLink {
	padding: 1em;
	display: block;
	cursor: pointer;
}
.EventLink:hover {
	background: #c2d6bd;
}

.EventLinks-heading {
	display: -ms-flexbox;
	display: flex;
	padding: 0.5em 1em;
	-ms-flex-align: center;
	    align-items: center;
	border-bottom: 1px solid #c2d6bd;
	-ms-flex-direction: row;
	    flex-direction: row;
}

.EventLinks-heading > small, .EventLinks-close {
	display: block;
	-ms-flex: 1 1;
	    flex: 1 1;
}

.EventLinks-close {
	border: none;
	cursor: pointer;
	background: none;
	text-align: right;
}

.ComingSoon {
	text-align: center;
	color: rgba(0, 0, 0, 0.5);
}

.EventLink-shortDescription {
	font-size: 0.8em;
	color: rgba(0, 0, 0, 0.8);
	margin-top: 0.3em;
}

.Event-loading {
	padding: 1em;
}




