.loader {
  display: block;
  margin: auto;
  width: 20px;
  height: 20px;
  position: relative;
  border: 2px solid #Fff;
  opacity: 0.5;
  -webkit-animation: loader 2s infinite cubic-bezier(.47,.13,.29,.99);
          animation: loader 2s infinite cubic-bezier(.47,.13,.29,.99);
}

.loader-inner {
  vertical-align: top;
  display: inline-block;
  width: 100%;
  background-color: #fff;
  -webkit-animation: loader-inner 2s infinite cubic-bezier(.47,.13,.29,.99);
          animation: loader-inner 2s infinite cubic-bezier(.47,.13,.29,.99);
}

@-webkit-keyframes loader {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  25% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  75% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes loader {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  25% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  75% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes loader-inner {
  0% {
    height: 0%;
  }

  25% {
    height: 0%;
  }

  50% {
    height: 100%;
  }

  75% {
    height: 100%;
  }

  100% {
    height: 0%;
  }
}

@keyframes loader-inner {
  0% {
    height: 0%;
  }

  25% {
    height: 0%;
  }

  50% {
    height: 100%;
  }

  75% {
    height: 100%;
  }

  100% {
    height: 0%;
  }
}

@-webkit-keyframes loader_ {
  0% {
    height: 20px;
    width: 20px;
    background-color: #fff;
  }

  50% {
    height: 0%;
    width: 100%;
    background-color: #000;
  }

  100% {
    height: 100%;
    width: 100%;
    background-color: #000;
  }
}

@keyframes loader_ {
  0% {
    height: 20px;
    width: 20px;
    background-color: #fff;
  }

  50% {
    height: 0%;
    width: 100%;
    background-color: #000;
  }

  100% {
    height: 100%;
    width: 100%;
    background-color: #000;
  }
}


