.Activities {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	height: 100vh;
	-ms-flex-direction: row;
	    flex-direction: row;
	width: 100%;
	overflow: hidden;
}

.Activity-wrapper {
	height: 100%;
	-ms-flex: 1 1;
	    flex: 1 1;
	text-align: center;
	-webkit-transition: flex 500ms;
	-o-transition: flex 500ms;
	transition: flex 500ms;
	transition: flex 500ms, -ms-flex 500ms;
}

.Activity-cover {
	height: 100%;
}

.Activity-wrapper:nth-child(even) {
	background-color: #E6FFE0;
	color: #196070;
}

.Activity-wrapper:nth-child(odd) {
	background-color: #196070;
	color: #E6FFE0;
}

.Activity {
	top: 0;
	width: 100%;
	height: 100%;
	cursor: pointer;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}

.vertically-aligned {
	display: -ms-flexbox;
	display: flex;
	width: 100%;
	-ms-flex-direction: column;
	    flex-direction: column;
	-webkit-transition: all 1s;
	-o-transition: all 1s;
	transition: all 1s;
}

.Expanded-box {
	margin: 1em;
}

.Activity-title {
	width: 100%;
	text-transform: uppercase;
}

.Activity-heading {
	font-size: 150%;
	font-weight: 500;
	display: block;
	margin: auto;
	vertical-align: middle;
}

.SelectedActivity-wrapper {
	width: 100%;
	margin-top: 5em;
}

.SelectedActivity {
	display: block;
	height: 100%;
	max-width: 800px;
	padding: 1em;
	margin: auto;
}

.SelectedActivity-title {
	font-size: 48px;
	font-weight: 300;
	color: #4C4B4D;
	text-transform: uppercase;
}

.SelectedActivity-link {
	display: inline-block;
}

.SelectedActivity-link:hover {
	text-decoration: underline;
}
.Button {
	text-decoration: none;
	background-color: transparent;
	padding: 1em;
	border: 1px solid;
	color: #E6FFE0;
	cursor: pointer;
}

.Button:hover {
	background-color: #E6FFE0;
	color: black;
}

@media only screen and (max-width: 768px) {
	.Activity, .Activity-wrapper {
		width: 100%;
	}

	.Activities {
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	.Activity-wrapper {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		    align-items: center;
	}

	.Activity {
		position: relative;
		vertical-align: middle;
		overflow: auto;
	}

	.Expanded-box {
		font-size: 12px;
	}
}

.SelectedActivity-button {
	position: absolute;
	right: 0;
	top: 0;
	margin: 1em;
}




